import React, { FunctionComponent } from 'react'
import styled from '@emotion/styled'
import { IGatsbyImageData } from 'gatsby-plugin-image'
import ProfileLeftCard from './ProfileLeftCard'

const Background = styled.div`
  width: 100%;
  padding: 0 20px;
  display: flex;
  justify-content: center;
  align-items: center;

  /* [Ï§ëÏöî] Í∏∞Ï°¥Ïùò Í≥†Ï†ï ÎÜíÏù¥ ÏÑ§Ï†ïÏùÑ Ï†úÍ±∞ÌïòÍ≥† autoÎ°ú Î≥ÄÍ≤Ω */
  height: auto;
  min-height: auto;
`

type profileProps = {
  profileImage: IGatsbyImageData
}

const Introduction: FunctionComponent<profileProps> = function ({
  profileImage,
}) {
  return (
    <Background>
      <ProfileLeftCard image={profileImage} />
    </Background>
  )
}

export default Introduction
