import React, { FunctionComponent } from 'react'
import styled from '@emotion/styled'
import PostItem from './PostItem'
import { PostListItemType } from 'types/PostItem.types'
import useInfiniteScroll from 'hooks/useInfiniteScroll'
import { useViewMode } from '../../contexts/ViewModeContext'

type PostListProps = {
  selectedCategory: string
  posts: PostListItemType[]
}

const PostListWrapper = styled.div`
  width: 100%;
`

const InfinitePostList: FunctionComponent<PostListProps> = ({ selectedCategory, posts }) => {
  const { containerRef, postList } = useInfiniteScroll(selectedCategory, posts)

  return (
    <PostListWrapper ref={containerRef}>
      {postList.map(({ node: { id, fields: { slug }, frontmatter } }) => (
        <PostItem {...frontmatter} link={slug} key={id} />
      ))}
    </PostListWrapper>
  )
}

const SimplePostList: FunctionComponent<PostListProps> = ({ posts }) => {
  return (
    <PostListWrapper>
      {posts.map(({ node: { id, fields: { slug }, frontmatter } }) => (
        <PostItem {...frontmatter} link={slug} key={id} />
      ))}
    </PostListWrapper>
  )
}

const PostList: FunctionComponent<PostListProps> = function ({
  selectedCategory,
  posts,
}) {
  const { viewMode } = useViewMode()

  if (viewMode === 'infinite') {
    return <InfinitePostList selectedCategory={selectedCategory} posts={posts} />
  }

  return <SimplePostList selectedCategory={selectedCategory} posts={posts} />
}

export default PostList