import React, { FunctionComponent, useEffect, useRef, useState, useMemo } from 'react'
import styled from '@emotion/styled'
import { PostListItemType } from 'types/PostItem.types'
import CategoryList from './CategoryList'
import PostList from './PostList'
import Pagination from './Pagination'
import { useViewMode } from '../../contexts/ViewModeContext'

type ListProps = {
  categoryList: { [key: string]: number }
  selectedCategory: string
  posts: PostListItemType[]
}

const ListContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 50px;
  position: relative;
  margin-bottom: 50px;
`

const PaginationArea = styled.div`
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: 100px;
`

const PostListArea = styled.div`
  width: 70%;
  max-width: 100%;
  
  /* ÌÉúÎ∏îÎ¶ø/Î™®Î∞îÏùºÏóêÏÑúÎäî Ïπ¥ÌÖåÍ≥†Î¶¨Í∞Ä ÏóÜÏúºÎØÄÎ°ú 100% ÏÇ¨Ïö© */
  @media (max-width: 1024px) {
    width: 100%;
  }
`

const POSTS_PER_PAGE = 8

const List: FunctionComponent<ListProps> = function ({
  selectedCategory,
  categoryList,
  posts,
}) {
  const { viewMode } = useViewMode()
  const [postListHeight, setPostListHeight] = useState<number>(0)
  const postListRef = useRef<HTMLDivElement>(null)
  
  const [currentPage, setCurrentPage] = useState(1)

  useEffect(() => {
    setCurrentPage(1)
  }, [selectedCategory])

  useEffect(() => {
    if (postListRef.current) {
      const updateHeight = () => {
        setPostListHeight(postListRef.current?.clientHeight || 0)
      }
      const timer = setTimeout(updateHeight, 100)
      return () => clearTimeout(timer)
    }
  }, [selectedCategory, posts, currentPage, viewMode])

  const filteredPosts = useMemo(() => {
    if (selectedCategory === 'All') return posts
    
    return posts.filter(({ node: { frontmatter } }: PostListItemType) => {
      const category = frontmatter.category
      
      if (Array.isArray(category)) {
        return category.includes(selectedCategory)
      }
      
      return category === selectedCategory
    })
  }, [selectedCategory, posts])

  const currentPosts = useMemo(() => {
    if (viewMode === 'infinite') return filteredPosts
    
    const start = (currentPage - 1) * POSTS_PER_PAGE
    const end = start + POSTS_PER_PAGE
    return filteredPosts.slice(start, end)
  }, [currentPage, filteredPosts, viewMode])

  const totalPage = Math.ceil(filteredPosts.length / POSTS_PER_PAGE)

  return (
    <>
      <ListContainer>
        <CategoryList
          selectedCategory={selectedCategory}
          categoryList={categoryList}
          height={postListHeight}
        />
        
        <PostListArea ref={postListRef}>
          <PostList 
            selectedCategory={selectedCategory} 
            posts={currentPosts} 
          />
        </PostListArea>
      </ListContainer>

      {viewMode === 'pagination' && filteredPosts.length > 0 && (
        <PaginationArea>
          <Pagination 
            totalPage={totalPage} 
            currentPage={currentPage} 
            onChange={setCurrentPage} 
          />
        </PaginationArea>
      )}
    </>
  )
}

export default List