import React, { FunctionComponent, useMemo } from 'react'
import Profile from 'components/Main/Profile'
import { graphql } from 'gatsby'
import { IGatsbyImageData } from 'gatsby-plugin-image'
import { PostListItemType } from 'types/PostItem.types'
import queryString, { ParsedQuery } from 'query-string'
import { CategoryListProps } from 'components/Main/CategoryList'
import Layout from 'components/Layout/Layout'
import List from 'components/Main/List'

type IndexPageProps = {
  location: {
    search: string
  }
  data: {
    site: {
      siteMetadata: {
        title: string
        description: string
        siteUrl: string
      }
    }
    allMarkdownRemark: {
      edges: PostListItemType[]
    }
    profile: {
      profileChildImageSharp: {
        profileGatsbyImageData: IGatsbyImageData
      }
    }
    study: {
      studyChildImageSharp: {
        studyGatsbyImageData: IGatsbyImageData
      }
    }
  }
}

const IndexPage: FunctionComponent<IndexPageProps> = function ({
  location: { search },
  data: {
    site: {
      siteMetadata: { title, description, siteUrl },
    },
    allMarkdownRemark: { edges },
    profile: {
      profileChildImageSharp: { profileGatsbyImageData },
    },
    study: {
      studyChildImageSharp: { studyGatsbyImageData },
    },
  },
}) {
  const parsed: ParsedQuery<string> = queryString.parse(search)
  const selectedCategory: string =
    typeof parsed.category !== 'string' || !parsed.category
      ? 'All'
      : parsed.category

  const sortedEdges = useMemo(() => {
    return [...edges].sort((a, b) => {
      const dateA = a.node.frontmatter.update || a.node.frontmatter.date
      const dateB = b.node.frontmatter.update || b.node.frontmatter.date
      
      if (dateA < dateB) return 1
      if (dateA > dateB) return -1
      return 0
    })
  }, [edges])

  const categoryList = useMemo(
    () =>
      sortedEdges.reduce( 
        (
          list: CategoryListProps['categoryList'],
          {
            node: {
              frontmatter: { category },
            },
          },
        ) => {
          if (list[category] === undefined) list[category] = 1
          else list[category]++

          list['All']++

          return list
        },
        { All: 0 },
      ),
    [sortedEdges],
  )

  return (
    <Layout title={title} description={description} url={siteUrl} image={''}>
      <Profile
        studyImage={studyGatsbyImageData}
        profileImage={profileGatsbyImageData}
      />
      <List
        selectedCategory={selectedCategory}
        categoryList={categoryList}
        posts={sortedEdges}
      />
    </Layout>
  )
}

export default IndexPage

export const getPostList = graphql`
  query getPostList {
    site {
      siteMetadata {
        title
        description
        siteUrl
      }
    }
    allMarkdownRemark(
      sort: [{ frontmatter: { date: DESC } }, { frontmatter: { title: DESC } }]
    ) {
      edges {
        node {
          id
          fields {
            slug
          }
          frontmatter {
            title
            summary
            date(formatString: "YYYY.MM.DD.")
            update(formatString: "YYYY.MM.DD.") # update ÌïÑÎìú ÌïÑÏàò
            category
            tags
            thumbnail {
              childImageSharp {
                gatsbyImageData(width: 768, height: 1200)
              }
            }
          }
        }
      }
    }
    profile: file(name: { eq: "profile" }) {
      profileChildImageSharp: childImageSharp {
        profileGatsbyImageData: gatsbyImageData
      }
    }
    study: file(name: { eq: "study" }) {
      studyChildImageSharp: childImageSharp {
        studyGatsbyImageData: gatsbyImageData
      }
    }
  }
`